.MonsterDecks--Header--Button {
    height: 64px;
    margin-right: 12px;
    margin-bottom: 12px;
    background-color: inherit;
}

.MonsterDecks--Header--ButtonReady {
    -webkit-animation: pulse 1.2s infinite;
            animation: pulse 1.2s infinite;
}

@-webkit-keyframes pulse {
    0%, 100% {
      background-color: rgb(28, 119, 255);
    }
    30%, 40% {
      background-color: rgb(30, 255, 255);
    }
}

@keyframes pulse {
    0%, 100% {
      background-color: rgb(28, 119, 255);
    }
    30%, 40% {
      background-color: rgb(30, 255, 255);
    }
}

.MonsterDecks {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.MonsterDecks--Deck {
    position: relative;
}



